.app-wrapper {
  max-width: 1280px;
  height: 100vh;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
    "h h "
    "n c ";
}
.content {
  height: 100%;
  overflow-y: hidden;
  grid-area: c;
  background-color: #c0efe7;
}
